:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background: peachpuff;
  color: #424242;
  padding: 30px;
}
.search-field {
  border: 1px solid grey;
  border-radius: 4px;
  padding: 10px;
  font-size: 20px;
  margin-right: 20px;
  width: 300px;
}
.btn {
  border: 1px solid grey;
  border-radius: 4px;
  padding: 10px 20px;
  background-color: orange;

  font-size: 20px;
}
.gallery {
  width: 1200px;
  display: flex;
  list-style-type: none;
  flex-wrap: wrap;
  padding: 0;
}
.img-thumb {
  width: 300px;
  height: 204px;
  display: flex;
}
.photo-card__img {
  height: 100%;
  width: 100%;
  object-fit: fill;
  display: block;
}
.gallery__item {
  margin-right: 20px;
  margin-bottom: 20px;
}
.photo-card {
  width: 300px;
}
